import React, { Suspense } from "react";

import { Environment } from "@react-three/drei";
import BASEPATH from "../../basepath";

function Lights() {
  return (
    <>
      <ambientLight intensity={0.3} color={"#ffffff"} />
      <pointLight position={[-5, 30, -12]} intensity={0.3} />
      <mesh position={[-5, 30, -12]} scale={0.5}>
        <sphereBufferGeometry attach="geometry" />
        <meshBasicMaterial attach="material" />
      </mesh>
      <spotLight
        castShadow
        //shadow-radius={0.5}
        shadow-bias={-0.001}
        position={[-10, 20, 10]}
        intensity={1.2}
        shadow-mapSize-width={512}
        shadow-mapSize-height={512}
        angle={Math.PI / 5}
        penumbra={1}
      />
      <mesh position={[-10, 20, 10]} scale={1.5}>
        <sphereBufferGeometry attach="geometry" />
        <meshBasicMaterial attach="material" />
      </mesh>
      <pointLight position={[10, 20, -5]} intensity={0.6} color={"#fffaf0"} />
      <mesh position={[10, 20, -5]} scale={0.5}>
        <sphereBufferGeometry attach="geometry" />
        <meshBasicMaterial attach="material" />
      </mesh>
      <pointLight position={[-10, -10, -3]} intensity={0.6} color={"#fffaf0"} />
      <mesh position={[-10, -10, -3]} scale={0.5}>
        <sphereBufferGeometry attach="geometry" />
        <meshBasicMaterial attach="material" />
      </mesh>
      <pointLight position={[10, -10, -5]} intensity={0.2} color={"#fffaf0"} />
      <mesh position={[10, -10, -5]} scale={0.5}>
        <sphereBufferGeometry attach="geometry" />
        <meshBasicMaterial attach="material" />
      </mesh>
      {/* <primitive object={directionalLight1.target} position={[0, 0, 0]}/> */}
      <Suspense fallback={null}>
        <Environment
          background={false} // Whether to affect scene.background
          files="abandoned_workshop_1k_small.hdr" // Array of cubemap files OR single equirectangular file
          path={BASEPATH + "/"} // Path to the above file(s)
          //preset={null} // Preset string (overrides files and path)
        />
      </Suspense>

      {/* <ContactShadows width={200} height={200} position-y={-10} far={20} /> */}
      {/* <Sky sunPosition={[100, -3, -50]} distance={1000} /> */}
      {/* <fog args={["white", 10, 50]} /> */}
    </>
  );
}

export default Lights;
