function gaussianRand(n) {
  var rand = 0;
  for (var i = 0; i < n; i++) rand += Math.random();
  return rand / n;
}

function Particle({
  model,
  radius = 1,
  randomness = 0.08,
  mouse = { current: [0, 0] },
  count = 100,
  log = false,
}) {
  const mesh = useRef();
  const light = useRef();
  const { size, viewport } = useThree();
  const aspect = size.width / viewport.width;

  const dummy = useMemo(() => new THREE.Object3D(), []);
  // Generate some random positions, speed factors and timings
  const particles = useMemo(() => {
    const temp = [];
    const phiSeg = (2 * Math.PI) / 3;
    const phiOffset = Math.PI / 6;
    for (let i = 0; i < count; i++) {
      const t = Math.random() * 100;
      const factor = 20 + Math.random() * 100;
      const speed = 0.002 + (2 * Math.random()) / 1000;
      const xFactor = -50 + Math.random() * 100;
      const yFactor = -50 + Math.random() * 100;
      const zFactor = -50 + Math.random() * 100;
      const phi = Math.PI * gaussianRand(1001);
      temp.push({
        t,
        factor,
        speed,
        xFactor,
        yFactor,
        zFactor,
        phi,
        mx: 0,
        my: 0,
        maxfx: 0,
      });
    }
    return temp;
  }, [count]);
  const globalTime = useMemo(() => {
    const t = 0;
    return { t };
  }, []);
  // The innards of this hook will run every frame
  useFrame((state) => {
    // Makes the light follow the mouse
    /*
    light.current.position.set(
      mouse.current[0] / aspect,
      -mouse.current[1] / aspect,
      0
    );*/

    var vec = new THREE.Vector3(); // create once and reuse
    var pos = new THREE.Vector3(); // create once and reuse

    vec.set(
      (mouse.current[0] / window.innerWidth) * 2 - 1,
      -(mouse.current[1] / window.innerHeight) * 2 + 1,
      0.5
    );

    /*
    const { camera } = state;

    vec.unproject(camera);

    vec.sub(camera.position).normalize();

    var distance = (0 - camera.position.z) / vec.z;

    pos.copy(camera.position).add(vec.multiplyScalar(distance));*/

    //console.log(mouse.current);

    particles.forEach((particle, i) => {
      let { t, factor, speed, xFactor, yFactor, zFactor, phi, maxfx } =
        particle;
      const s = (1 - 0.2 * (0.5 + Math.cos(t / 12) / 2)) * 0.35;
      t = particle.t += speed / 2 / s / 5;
      const a = Math.cos(t) + Math.sin(t * 1) / 10;
      const b = Math.sin(t) + Math.cos(t * 2) / 10;
      particle.mx += (pos.x - particle.mx) * 0.01;
      particle.my += (pos.z - particle.my) * 0.01;
      const PI = Math.PI;
      const TP = 2 * Math.PI;
      const fx =
        (particle.mx / 10) * a +
        //xFactor +
        Math.cos((t / 10) * factor) +
        (Math.sin(t * 1) * factor) / 10;
      let fy =
        (particle.my / 10) * b +
        //yFactor +
        Math.sin(((t * 1) / 10) * factor) +
        (Math.cos(t * 3 * 2) * factor) / 10;
      // fy = ((fy % TP) + TP) % TP;
      const fz =
        ((particle.my / 10) * b +
          zFactor +
          Math.cos((t / 10) * factor) +
          (Math.sin(t * 3) * factor) / 10) %
        TP;

      maxfx = particle.maxfx = Math.max(maxfx, Math.abs(fx));

      const theta = t + fx / 200;
      //const fxfac = (15 * fx) / maxfx;
      const fxfac = 15 + Math.sin(globalTime.t / 20) * 8;
      if (log && i == 0) {
        //console.log(s, t);
        //console.log(fxfac);
      }
      phi += fy / 20; //fxfac;

      dummy.position.set(
        radius * Math.cos(theta) * Math.sin(phi),
        radius * Math.cos(phi),
        radius * Math.sin(theta) * Math.sin(phi)
      );
      dummy.scale.set(s, s, s);
      dummy.rotation.set(fx, fz, fy);
      dummy.updateMatrix();
      mesh.current.setMatrixAt(i, dummy.matrix);
    });
    globalTime.t += 1;
    mesh.current.instanceMatrix.needsUpdate = true;
  });

  return (
    <instancedMesh
      ref={mesh}
      args={[model.geometry, model.material, count]}
    ></instancedMesh>
  );
}

export default Particle;

