var mix = function (color_1, color_2, weight) {
  function d2h(d) {
    return d.toString(16);
  } // convert a decimal value to hex
  function h2d(h) {
    return parseInt(h, 16);
  } // convert a hex value to decimal

  weight = typeof weight !== "undefined" ? weight : 50; // set the weight to 50%, if that argument is omitted

  var color = "#";

  for (var i = 0; i <= 5; i += 2) {
    // loop through each of the 3 hex pairsâ€”red, green, and blue
    var v1 = h2d(color_1.substr(i + 1, 2)), // extract the current pairs
      v2 = h2d(color_2.substr(i + 1, 2)),
      // combine the current pairs from each source color, according to the specified weight
      val = d2h(Math.floor(v2 + (v1 - v2) * (weight / 100.0)));

    while (val.length < 2) {
      val = "0" + val;
    } // prepend a '0' if val results in a single digit

    color += val; // concatenate val to our new color string
  }

  return color; // PROFIT!
};

function Box(props) {
  // This reference will give us direct access to the mesh
  const mesh = useRef();
  // Set up state for the hovered and active state
  const [hovered, setHover] = useState(false);
  const [active, setActive] = useState(false);
  // Subscribe this component to the render-loop, rotate the mesh every frame
  useFrame((state, delta) => (mesh.current.rotation.x += 0.01));
  // Return view, these are regular three.js elements expressed in JSX
  return (
    <mesh
      {...props}
      ref={mesh}
      scale={active ? 1.5 : 1}
      onClick={(event) => setActive(!active)}
      onPointerOver={(event) => setHover(true)}
      onPointerOut={(event) => setHover(false)}
    >
      <boxGeometry args={[1, 1, 1]} />
      <meshStandardMaterial color={hovered ? "hotpink" : "orange"} />
    </mesh>
  );
}

function hexToRgb(hex) {
  var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
  return result
    ? {
        r: parseInt(result[1], 16) / 255.0,
        g: parseInt(result[2], 16) / 255.0,
        b: parseInt(result[3], 16) / 255.0,
      }
    : null;
}

function GroundPlane({ bgcolor }) {
  const alphaMap = useLoader(TextureLoader, BASEPATH + "/alphamap.jpg");

  const [currentColor, setCurrentColor] = useState(bgcolor);
  const color = useRef(bgcolor);
  const time = useRef(0);
  const mat = useRef();
  const STEP = 0.01;

  useFrame(({ clock }) => {
    const now = clock.getElapsedTime();
    //console.log(now);
    if (now - time.current > STEP) {
      time.current = now;
      const c = mix(bgcolor, color.current, 3); // 3%
      color.current = c;
      mat.current.color = hexToRgb(c);
    }
    //console.log(mat.current.color);
  });

  return (
    <Plane
      args={[100, 100]}
      receiveShadow
      rotation-x={-Math.PI / 2}
      position={[0, -12, 0]}
      //onClick={() => setActive(Number(!active))}
    >
      <meshPhysicalMaterial
        //meshStandardMaterial

        ref={mat}
        attach="material"
        //color={bgcolor}
        roughness={0.5}
        transparent
        opacity={0.6}
        alphaMap={alphaMap}
      />
    </Plane>
  );
}

function hashToURL(hash) {
  const parts = hash.split("/");
  hash = parts[0];
  let cid = new CID(hash).toV0();
  const path = parts.length > 1 ? parts[1] : "";

  console.log(cid.toString(), cid.version);
  const cidv1 = cid.toV1();

  const hashv0 = cid.toString();
  console.log(hashv0);

  const hashv1 = cidv1.toBaseEncodedString("b");
  console.log(hashv1);

  return `https://${hashv1}.ipfs.dweb.link/${path}`;
  //`https://ipfs.io/ipfs/${default_hash}`
  //`https://gateway.pinata.cloud/ipfs/Qmf6gwEkmkW6VTEsku8nenQVroV9A7wuPKKSX7YCtbNwmf`
  //`https://ipfs.kaleido.art/ipfs/${default_hash_new}`;
  //`https://hardbin.com/ipfs/${default_hash_new}`;
}

