import React, { useEffect } from "react";

import { useGLTF, useAnimations } from "@react-three/drei";
import BASEPATH from "../../basepath";

function Halo({ url = BASEPATH + "/halo-gold.glb", receiveShadow = false }) {
  const { scene, nodes, materials, animations } = useGLTF(url);

  const { ref, mixer, names, actions, clips } = useAnimations(
    animations,
    scene
  );

  console.log("nodes", nodes);
  useEffect(() => {
    console.log("actions:", actions);
    Object.values(actions).forEach((action, i) => {
      action.play();
    });
  }, []);

  for (let child of Object.values(nodes)) {
    child.castShadow = true;
    child.receiveShadow = Boolean(receiveShadow);
    if (child.material) {
      const mat = child.material;
      mat.aoMap = null;
      mat.envMapIntensity = 0.3;
      if (mat.metalness > 0) mat.envMapIntensity = 0.75;
      //mat.flatShading;
      //mat.toneMapped = false;
    }
  }
  console.log("cubes", nodes);
  return (
    <group scale={[1, 1, 1]} position={[0, 7, 0]}>
      <primitive object={scene} />
    </group>
  );
}

export default Halo;
